.log .filters {
  margin-bottom: 30px;
}
.log .output {
  background-color: #F7F8FB;
  border: 1px solid #E7EBF1;
  padding: 10px;
  max-height: 200px;
  overflow: auto;
}
.log .output.empty {
  height: 200px;
  position: relative;
}
.log .output.empty:before {
  content: 'No logs available';
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.log .output pre {
  white-space: pre-wrap;
  margin: 0;
}
.log .output span.info {
  color: #437BD7;
}
.log .output span.warn {
  color: #D78043;
}
.log .output span.error {
  color: #D74343;
}



