.metrics.empty:before {
  content: 'Pulling node metrics...';
}
.metrics > svg {
  height: 0;
  display: block;
}
.metrics .charts {
  margin-top: 30px;
  position: relative;
}
.metrics .chart {
  font-size: 14px;
  border: 1px solid #E7EBF1;
}
.metrics .chart + .chart {
  margin-top: 30px;
}
.metrics .chart > div {
  padding: 30px;
}
.metrics .chart p {
  text-align: center;
  margin: 0 auto;
  padding: 15px 0;
  border-bottom: 1px solid #E7EBF1;
}
.metrics .chart.events p {
  margin-bottom: 30px;
}
.metrics .chart svg {
  margin: 0 auto;
}
.metrics .chart svg text {
  fill: black !important;
}
.metrics .chart svg rect:not(.eventchart-marker) + text {
  text-anchor: end !important;
}
.metrics .chart svg rect:not(.eventchart-marker) ~ text {
  font-size: 14px !important;
  -webkit-transform: translateX(41px);
  -ms-transform: translateX(41px);
  transform: translateX(41px);
}
.metrics .chart svg line[pointer-events] ~ g text {
  -webkit-transform: translateY(-2px);
      -ms-transform: translateY(-2px);
          transform: translateY(-2px);
}
.metrics .chart svg line[pointer-events] ~ g text tspan:first-child {
  display: none;
}
.metrics .chart > div > div:last-child {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  margin: 15px 0 0 !important;
  pointer-events: none;
}
.metrics .chart.events > div > div:last-child * {
  opacity: 1 !important;
}
.metrics .chart.events > div > div svg {
  margin-right: 7px;
}
.metrics .chart .eventchart-marker {
  width: 6px !important;
}
.metrics .chart .eventchart-marker:hover {
  fill: #E7EBF1 !important;
  opacity: 1 !important;
}
.metrics .chart .eventchart-marker + g {
  -webkit-transform: translate(4px, 4px);
      -ms-transform: translate(4px, 4px);
          transform: translate(4px, 4px);
}
.metrics .chart .eventchart-marker + g .eventchart-marker {
  fill: none !important;
}



