::-moz-selection {
  background: #E8EAF1;
}
::selection {
  background: #E8EAF1;
}
::-moz-selection {
  background: #E8EAF1;
}
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  background-color: #FCFDFF;
  font-family: 'Inconsolata';
  font-size: 16px;
  margin: 0;
  padding: 0;
}
p, span, pre, code, text {
  cursor: default;
}
a {
  color: black;
}
input, select, button, pre, code, text {
  font-family: 'Inconsolata';
  font-size: 14px;
}
input, select, button {
  height: 40px;
}
input:focus, select:focus, button:focus {
  outline: none;
}
input, select {
  border: 1px solid #E7EBF1;
  padding: 0 10px;
}
input, button {
  width: 100%;
}
input {
  margin: 0;
}
input:focus {
  background-color: #F7F8FB;
}
select {
  background: -webkit-linear-gradient(45deg, transparent 50%, black 50%), -webkit-linear-gradient(315deg, black 50%, transparent 50%);
  background-position: calc(100% - 15px) 50%, calc(100% - 10px) 50%;
  background-size: 5px 5px, 5px 5px;
  background-repeat: no-repeat;
  border-radius: 0;
  width: 200px;
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
button {
  background-color: #00A500;
  color: white;
  border: none;
  display: block;
  position: relative;
  cursor: pointer;
}
button span {
  position: relative;
  z-index: 1;
  pointer-events: none;
}
button:before {
  content: '';
  background-color: rgba(0, 0, 0, 0.1);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
  -webkit-transition: -webkit-transform 0.2s ease-in-out;
  transition: -webkit-transform 0.2s ease-in-out;
  -o-transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
button:hover:before {
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
}
button:disabled {
  background-color: #93979E !important;
  cursor: not-allowed;
}
button:disabled:hover:before {
  content: none;
}
button.orange {
  background-color: #FFA500;
}
button.red {
  background-color: #A50000;
}
code {
  color: #93979E;
  background-color: #F7F8FB;
  border: 1px solid #E7EBF1;
  padding: 2px 5px;
}
.app {
  min-width: 900px;
  max-width: 1400px;
  padding: 0 60px;
  margin: 0 auto;
}
@media (max-width: 1100px) {
  .app {
    padding: 0 20px;
  }
}



