.tabs {
  border: 1px solid #E7EBF1;
  border-bottom: none;
  margin: 0;
  padding: 0;
  display: inline-block;
  position: relative;
  top: 1px;
}
.tabs li {
  background-color: #E7EBF1;
  height: 40px;
  padding: 0 60px;
  cursor: default;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
  align-items: center;
}
.tabs li:hover {
  background-color: #DEE2E8;
}
.tabs li.active {
  background-color: white;
  font-weight: bold;
}



