.settings {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-between;
  justify-content: space-between;
}
.settings .form, .settings .details {
  width: calc(50% - 15px);
  border: 1px solid #E7EBF1;
  padding: 15px;
  line-height: 1.4;
}
.settings .form p {
  margin: 0;
}
.settings .form * + p {
  margin-top: 15px;
}
.settings .form button {
  margin-top: 15px;
}
.settings .details ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.settings .details p {
  margin: 0 10px 0 0;
  display: inline-block;
}
.settings .details li + li {
  margin-top: 10px;
}



